.Task {
	background: blue;
	display:-ms-flexbox;
	display:flex;
	margin:10px auto;
	max-width: 70%;
	min-height: 100px;
	border: 2px solid white;
	background: #222222;
	padding: 5px;
	color:white;
	position:relative;
}
.Task p, .Task h3 {
	margin-bottom: 0;
}

.Task button {
	background: none;
	border: none;
	color: white;
}
.Task textarea {
	resize: none;
	width: 100%;
	background: none;
}

.TaskInfo {
	width: 60%;
}

.TaskTitle {
	display: -ms-flexbox;
	display: flex;
	height: 30px;
}
.TaskTitle button {
	font-size: 1.4em;
}

.TaskProgress {
	width: 40%;
	position:relative;
}

.TaskProgress textarea {
	position:absolute;
	top:30px;
	bottom:0px;
	color:white;
}

.TaskButtons {
	position:absolute;
	right:5px;
	font-size: 1.5em;
	top:0;
}

.dd-menu-items {
	position:absolute;
	list-style: none;
	background: white;
	color: black;
	font-size: .8em;
	border-radius: 5px;
}

.dd-items-right button {
	color:black;
}

.dd-items-right li:hover {
	background: gray;
}

.dd-items-right {
	list-style: none;
	padding-left: 0;
	padding-right: 0;
	margin-bottom: 0px;
}



